import React, {
    useRef,
    useEffect,
    forwardRef,
    useImperativeHandle
} from 'react';
import { MDCFormField } from '@material/form-field';
import clsx from 'clsx';
import {
    FormFieldDefaultProps,
    FormFieldPropTypes
} from './generated/propTypes';

const FormField = forwardRef((props, externalRef) => {
    const { children, inputRef, className, alignEnd, ...customProps } = props;
    const ref = useRef();
    const mdcFormField = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    useEffect(() => {
        mdcFormField.current = MDCFormField.attachTo(ref.current);
        return () => {
            mdcFormField.current.destroy();
        };
    });

    useEffect(() => {
        if (inputRef) {
            mdcFormField.current.input = inputRef.current;
        }
    }, [inputRef]);

    return (
        <div
            ref={ref}
            className={clsx(
                'lmnt mdc-form-field lmnt-form-field',
                alignEnd && 'mdc-form-field--align-end',
                className
            )}
            {...customProps}
        >
            {children}
        </div>
    );
});

FormField.displayName = 'FormField';
FormField.propTypes = FormFieldPropTypes;
FormField.defaultProps = FormFieldDefaultProps;

export default FormField;
